body {
  color: #f8f8f2;
  background: #282a36;
}

a {
  color: #50fa7b;
}

a:hover {
  color: #90faaa;
}

body > .container {
  margin-top: 20vh;
}

.name {
  color: #ce6358;
}

.vector-selfie {
  max-width: 100%;
}

.speech-bubble {
  cursor: default;
  box-sizing: border-box;
  --bbArrowSize: 1.5rem;
  --bbBorderRadius: .2rem;
  --bbPadding: 1rem 3rem;
  background: var(--bbColor);
  border-radius: var(--bbBorderRadius);
  padding: var(--bbPadding);
  filter: drop-shadow(0 0 .2rem #282a36);
  margin: 0 0 10px;
  margin-left: var(--bbArrowSize);
  --bbColor: #44475a;
  letter-spacing: .01rem;
  grid-area: lc;
  transition: transform .25s;
  position: relative;
}

.speech-bubble:before {
  top: calc(50% - var(--height) / 2);
  clip-path: polygon(0 50%, 100% 12.5%, 100% 87.5%);
  --height: calc(var(--bbArrowSize) / 2 * 4);
  width: var(--bbArrowSize);
  height: var(--height);
  content: "";
  background: var(--bbColor);
  position: absolute;
  right: calc(100% - 2px);
}

.speech-bubble:first-of-type:before {
  clip-path: polygon(0 80%, 100% 12.5%, 100% 84.5%);
}

.speech-bubble:nth-of-type(4):before {
  clip-path: polygon(0 30%, 100% 12.5%, 100% 87.5%);
}

.icon {
  margin: 0 12px;
  font-size: 36px;
  text-decoration: none;
}

.contacts {
  text-align: center;
  margin: 8px -2px 0 0;
}

@media (width <= 768px) {
  .speech-bubble:before {
    display: none;
  }
}

